.p-load-image-container{background-color:#f6f6f6;background-size:cover;background-repeat:no-repeat;position:relative;overflow:hidden}.p-load-image-container canvas,.p-load-image-container img{position:absolute;opacity:0;top:0;left:0;-webkit-transition:opacity 1s linear;transition:opacity 1s linear}.p-load-image-container canvas.loaded,.p-load-image-container img.loaded{opacity:1}